<template>
  <v-container class="my-md-5">
    <v-card elevation="2">
      <div class="pa-5">
        <!-- <p>{{ answeredQuestions }}</p>
        <p>{{ unAnsweredQuestions }}</p> -->
        <!-- <p>{{ details }}</p> -->
        <h1 class="text-h5 font-weight-bold">{{ details.name }}</h1>
        <div v-if="!answeredQuestions && details.started_at==null">
          <br>
          <div v-html="details.description"></div>
          <v-btn
            @click="start"
            :loading="startLoading"
          >
            Start
          </v-btn>
        </div>
        <div v-else>
          <v-divider class="my-2"></v-divider>
          <div v-if="unAnsweredQuestions.length > 0">
            <v-row>
              <v-col v-if="unAnsweredQuestions[currentQuestion].group_title">
                <h2 class="text-h5 mb-4">{{ unAnsweredQuestions[currentQuestion].group_title }}</h2>
                <div v-html="unAnsweredQuestions[currentQuestion].group_description"></div>
              </v-col>
              <v-col>
                <div class="mt-3 mb-5">
                  <CountDown
                    v-if="details.expired_at"
                    :starttime="details.started_at" 
                    :endtime="details.expired_at"
                    trans='{  
                      "day":"Day",
                      "hours":"Hours",
                      "minutes":"Minutes",
                      "seconds":"Seconds",
                      "expired":"Event has been expired.",
                      "running":"Till the end of event.",
                      "upcoming":"Till start of event.",
                      "status": {
                        "expired":"Expired",
                        "running":"Running",
                        "upcoming":"Future"
                      }
                    }'
                    @expired="testEnd(true)"
                  />
                </div>
                <p class="text-caption font-weight-bold">Question {{ (details.questions.length - unAnsweredQuestions.length) + 1 }} of {{ details.questions.length }}</p>
                <p class="text-subtitle-1">{{ unAnsweredQuestions[currentQuestion].question }}</p>
                <div v-if="unAnsweredQuestions[currentQuestion].has_choices=='No'">
                  <v-form
                    ref="form"
                    v-model="valid"
                    @submit.prevent="handleFormAnswerSubmit(unAnsweredQuestions[currentQuestion].id)"
                  >
                    <!-- If Essay -->
                    <v-text-field
                      v-model="answer"
                      label="Enter your answer"
                      required
                      :rules="[rules.required, rules.minimum(100), rules.maximum(300)]"
                      counter="300"
                      maxlength="300"
                      hide-details="auto"
                      v-if="parseInt(details.id)==5"
                    ></v-text-field>

                    <!-- If Numerical Retake -->
                    <v-text-field
                      v-else
                      v-model="answer"
                      label="Enter your answer"
                      required
                      :rules="[rules.required]"
                    ></v-text-field>
                    <v-btn
                      :disabled="!valid"
                      type="submit"
                      color="blue darken-3 white--text"
                      :loading="formAnswerLoading"
                    >
                      Submit
                    </v-btn>
                  </v-form>
                </div>
                <div v-else>
                  <p class="text-caption font-italic grey--text">Choose answer</p>
                  <v-row>
                    <v-col 
                      cols="12" 
                      md="6"
                      v-for="(choice, index) in unAnsweredQuestions[currentQuestion].choices" 
                      :key="index"
                    >
                      <v-alert
                        ref="answerAlert"
                        elevation="0"
                        border="left"
                        class="px-8 alert-answer"
                        :style="[clickAnswerLoading ? { cursor: 'not-allowed' } : { cursor: 'pointer' }]"
                        @click="handleAnswerClick(unAnsweredQuestions[currentQuestion].id, choice.id, index)"
                      >
                        {{ choice.description }}
                      </v-alert>
                    </v-col>
                  </v-row>
                  <div style="position: relative">
                    <v-progress-linear
                      class="mt-1 mb-2"
                      indeterminate
                      color="grey"
                      absolute
                      v-if="clickAnswerLoading"
                    ></v-progress-linear>
                  </div>
                </div>
              </v-col>
            </v-row>
          </div>
          <div v-else>
            <h4 class="text-h6 mb-3">Test Result</h4>
            <p>Result: <span class="font-weight-bold" :class="(details.result=='Passed') ? 'success--text' : 'red--text'">{{ details.result }}</span></p>
            <p class="font-italic"><small>Passing Score</small> {{ details.passing_score }}</p>
            <p>Your score: {{ parseInt(details.correct_answer) }} / {{ parseInt(details.correct_answer) + parseInt(details.wrong_answer) }}</p>

            <div v-if="parseInt(details.id)==1 || parseInt(details.id)==2">
              <!-- Upload computation file -->
              <v-divider v-if="!details.computation_file"></v-divider>
              <v-form
                ref="form"
                v-model="validUpload"
                @submit.prevent="handleSubmitUpload"
                v-if="!details.computation_file"
              >
                <p class="text-subtitle-1 mt-3 mb-0">Upload your scratch paper</p>
                <v-file-input
                  ref="file"
                  :rules="[rules.required]"
                  v-model="file"
                  accept=".png,.jpg"
                  prepend-icon="mdi-camera"
                  show-size
                  @change="onFileChange"
                  @click:clear="clearFile"
                  label="Select File"
                ></v-file-input>
                <v-btn
                  :disabled="!validUpload"
                  type="submit"
                  color="blue darken-3 white--text"
                  :loading="uploadLoading"
                >
                  <v-icon left>
                    mdi-tray-arrow-up
                  </v-icon>
                  Upload
                </v-btn>
                <p class="mt-4" v-if="url">File preview</p>
                <v-card
                  elevation="5"
                >
                  <v-img
                    class="mt-3"
                    v-if="url"
                    :src="url"
                  ></v-img>
                </v-card>
              </v-form>
              <!-- End of upload computation file -->

              <v-alert
                dense
                type="success"
                v-if="details.computation_file"
                class="d-inline-block"
              >Scratch Paper Uploaded.</v-alert>
              <br v-if="details.computation_file">

              <v-btn
                @click="testEnd"
                v-if="details.computation_file"
              >
                <span v-if="details.hasNextTest">Proceed to next test</span>
                <span v-else>Continue</span>
              </v-btn>
            </div>
            <div v-else>
              <v-btn
                @click="testEnd"
              >
              <!-- v-if="details.computation_file" -->
                <span v-if="details.hasNextTest">Proceed to next test</span>
                <span v-else>Continue</span>
              </v-btn>
            </div>
          </div>
          <v-alert
            :value="!!clickAnswerError"
            dense
            outlined
            type="error"
            class="mt-5 mb-0"
            transition="scale-transition"
            icon="mdi-exclamation-thick"
          >
            {{ clickAnswerError }}
          </v-alert>
        </div>
      </div>
    </v-card>
  </v-container>
</template>

<script>
import CountDown from '@/components/applicant/CountDown'
import { mapState, mapActions } from "vuex"
import formRules from '@/helpers/formRules'

export default {
  props: {
    details: {
      type: Object,
      required: true
    }
  },
  components: { CountDown },
  data: () => ({
    rules: formRules,
    valid: true,
    currentQuestion: 0,
    answer: '',
    validUpload: true,
    file: null,
    url: null,
  }),
  computed: {
    ...mapState("applicant", ["startLoading", "startError", "clickAnswerLoading", "clickAnswerError", "formAnswerLoading", "formAnswerError", "uploadLoading", "uploadError"]),
    unAnsweredQuestions() {
      if (this.details.questions) {
        return this.details.questions.filter(question => {
          return !question.has_answered
        })
      } else {
        return 0
      }
    },
    answeredQuestions() {
      if (this.details.questions) {
        return this.details.questions.filter(question => {
          return question.has_answered
        }).length
      } else {
        return 0
      }
    }
  },
  methods: {
    ...mapActions("applicant", ["startExam", "clickAnswer", "formAnswer", "uploadSubmit"]),
    async start() {
      if (!this.startLoading) {
        const startData = {
          test_id: this.details.id,
          code: this.details.code
        }

        const res = await this.startExam(startData)

        this.testStarted(res.started_at, res.expired_at)
      }
    },
    testStarted(started_at, expired_at) {
      this.$emit('testStarted', this.details.id, started_at, expired_at)
    },
    async handleAnswerClick(question_id, choice_id, index) {
      if (!this.clickAnswerLoading) {
        this.$refs.answerAlert[index].$el.classList.remove('alert-answer')
        this.$refs.answerAlert[index].$el.classList.add('alert-answered')
      
        const answerData = {
          test_id: this.details.id,
          code: this.details.code, 
          question_id: question_id, 
          choice_id: choice_id
        }

        const res = await this.clickAnswer(answerData)

        if (!this.clickAnswerError) {
          this.testAnswered(question_id, 1, choice_id)

          this.$refs.answerAlert[index].$el.classList.remove('alert-answered')
          this.$refs.answerAlert[index].$el.classList.add('alert-answer')

          if (!this.unAnsweredQuestions.length) {
            if (res.retake) {
              this.$emit('openRetake')
            }
            
            if (this.details.passing_score=='0') { // if no passing score proceed to next test, this means that this is an essay
              this.testEnd()
            } else {
              this.$emit('updateResult', this.details.id, res.test_result.result, res.test_result.correct_answer, res.test_result.wrong_answer)
            }
          }
        }
      }
    },
    async handleFormAnswerSubmit(question_id) {
      if (!this.formAnswerLoading) {
        const answerData = {
          test_id: this.details.id,
          code: this.details.code, 
          question_id: question_id,
          answer: this.answer, 
        }

        const res = await this.formAnswer(answerData)

        if (!this.formAnswerError) {
          this.testAnswered(question_id, 2, this.answer)

          this.$refs.form.reset()
          
          if (!this.unAnsweredQuestions.length) {
            if (this.details.passing_score=='0') { // if no passing score proceed to next test, this means that this is an essay
              this.testEnd()
            } else {
              this.$emit('updateResult', this.details.id, res.test_result.result, res.test_result.correct_answer, res.test_result.wrong_answer)
            }
          }
        }
      }
    },
    testAnswered(question_id, type, answer) {
      this.$emit('testAnswered', this.details.id, question_id, type, answer)
    },
    onFileChange(e) {
      /* console.log(e)
      const file = e.target.files[0]; */
      this.url = URL.createObjectURL(e)
    },
    clearFile() {
      this.url = null
    },
    async handleSubmitUpload() {
      const uploadData = {
        test_id: this.details.id,
        code: this.details.code, 
        file: this.file
      }

      if (!this.uploadLoading) {
        const res = await this.uploadSubmit(uploadData)

        if (!this.uploadError) {
          this.validUpload = true
          this.file = null
          this.url = null
          this.$emit('updateComputationFile', this.details.id, res.file_name)
        }
      }
    },
    testEnd(is_expired=false) {
      if (is_expired==true) {
        // Update Result here

        // If numerical test expired, open retake exam
        if (this.details.id==1) {
          this.$emit('openRetake')
        }
      }

      this.$emit('updateTest', this.details.id)
    }
  },
  watch: {
    unAnsweredQuestions(val) {
      this.answer = (val.length) ? val[this.currentQuestion].raw_answer : ''
    }
  }
}
</script>

<style scoped>
.alert-answer {
  border: 1px solid #EEEEEE !important;
  background: #F5F5F5;
}
.alert-answer:hover {
  background: #EEEEEE;
}

.alert-answered {
  border: 1px solid #A5D6A7 !important;
  background: #C8E6C9;
}
</style>