<template>
  <v-container class="my-md-5">
    <v-card
      elevation="2"
    > 
      <v-form
        ref="form"
        v-model="valid"
        @submit.prevent="handleConfirm"
      >
        <div class="pa-5">
          <v-alert
            :value="!!formError"
            dense
            outlined
            type="error"
            transition="scale-transition"
            icon="mdi-exclamation-thick"
          >
            {{ formError }}
          </v-alert>
          <div class="mb-2">
            <small>* indicates required field</small>
          </div>
          <v-card-title>PERSONAL INFORMATION</v-card-title>
          <v-card-text>
            <v-row>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-text-field
                  ref="surname"
                  :rules="[rules.required, rules.alphaSpace, rules.minimum(2), rules.maximum(30)]"
                  v-model="surname"
                  label="Surname*"
                  counter="30"
                  maxlength="30"
                  hide-details="auto"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-text-field
                  ref="given_name"
                  :rules="[rules.required, rules.alphaSpace, rules.minimum(2), rules.maximum(30)]"
                  v-model="given_name"
                  label="Given Name*"
                  counter="30"
                  maxlength="30"
                  hide-details="auto"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-text-field
                  ref="middle_name"
                  :rules="[rules.required, rules.alphaSpace, rules.maximum(30)]"
                  v-model="middle_name"
                  label="Middle Name*"
                  counter="30"
                  maxlength="30"
                  hide-details="auto"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-text-field
                  ref="nickname"
                  :rules="[rules.alphaSpace, rules.minimum(2), rules.maximum(30)]"
                  v-model="nickname"
                  label="Nickname"
                  counter="30"
                  maxlength="30"
                  hide-details="auto"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12">
                <v-text-field
                  ref="address"
                  :rules="[rules.required, rules.maximum(200)]"
                  v-model="address"
                  label="Address*"
                  counter="200"
                  maxlength="200"
                  hide-details="auto"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-menu
                  v-model="birthdate_menu"
                  :close-on-content-click="false"
                  transition="scale-transition"
                  offset-y
                  min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      ref="birthdate"
                      :rules="[rules.required]"
                      v-model="birthdate"
                      label="Birthdate*"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      @keyup="birthdate_menu=true"
                      @keydown="birthdate_menu=false"
                      hide-details="auto"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="birthdate"
                    @input="birthdate_menu = false"
                    :max="(new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)"
                  ></v-date-picker>
                </v-menu>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-text-field
                  v-model="getAge"
                  label="Age"
                  readonly
                  hide-details="auto"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-select
                  ref="gender"
                  :rules="[rules.required]"
                  v-model="gender"
                  :items="gender_choices"
                  label="Gender*"
                  hide-details="auto"
                ></v-select>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-select
                  ref="civil_status"
                  :rules="[rules.required]"
                  v-model="civil_status"
                  :items="civil_status_choices"
                  label="Civil Status*"
                  hide-details="auto"
                ></v-select>
              </v-col>
            </v-row>
            <v-row>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-text-field
                  ref="contact"
                  :rules="[rules.required, rules.validMobileNumber]"
                  v-model="contact"
                  label="Contact Number(Preferred Viber)*"
                  hint="must be a valid mobile number, example: 09293158777"
                  persistent-hint
                  counter="11"
                  maxlength="11"
                  hide-details="auto"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-text-field
                  :value="details.email"
                  label="Email Address"
                  readonly
                  hide-details="auto"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-text-field
                  ref="tin"
                  :rules="[rules.required, rules.numeric, rules.maximum(12)]"
                  v-model="tin"
                  label="TIN*"
                  counter="12"
                  maxlength="12"
                  hide-details="auto"
                  type="text"
                ></v-text-field>
              </v-col>
              <!-- <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-text-field
                  ref="sss"
                  :rules="[rules.numeric, rules.maximum(20)]"
                  v-model="sss"
                  label="SSS"
                  counter="20"
                  maxlength="20"
                  hide-details="auto"
                ></v-text-field>
              </v-col> -->
            </v-row>
          </v-card-text>
        </div>
        <v-divider class="mt-2"></v-divider>
        <div class="pa-5">
          <v-card-title>EDUCATIONAL ATTAINMENT</v-card-title>
          <v-card-text>
            <v-row v-for="(educ, index) in educational_attainment" :key="educ.id">
              <v-col
                cols="12"
                v-if="index > 0"
              >
                <v-divider class="brown lighten-5"></v-divider>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-text-field
                  v-model="educ.level"
                  label="Level"
                  counter="50"
                  maxlength="50"
                  hide-details="auto"
                  :rules="[rules.maximum(50)]"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="6"
              >
                <v-text-field
                  v-model="educ.school"
                  label="School"
                  counter="150"
                  maxlength="150"
                  hide-details="auto"
                  :rules="[rules.maximum(150)]"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-text-field
                  v-model="educ.course"
                  label="Course"
                  counter="50"
                  maxlength="50"
                  hide-details="auto"
                  :rules="[rules.maximum(50)]"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                v-if="index > 0"
              >
                <v-btn
                  color="red darken-1"
                  class="text-capitalize"
                  @click="removeEduc(educ.id)"
                  small
                  outlined
                >
                  <v-icon left>
                    mdi-delete-outline
                  </v-icon>
                  Remove
                </v-btn>
              </v-col>
            </v-row>
            <v-btn
              color="info"
              class="mt-5"
              small
              @click="addEduc"
              outlined
            >
              <v-icon left>
                mdi-plus
              </v-icon>
              Add Education
            </v-btn>
          </v-card-text>
        </div>
        <v-divider class="mt-2"></v-divider>
        <div class="pa-5">
          <v-card-title>RECENT WORK EXPERIENCE</v-card-title>
          <v-card-text>
            <v-row v-for="(work, index) in work_experience" :key="work.id">
              <v-col
                cols="12"
                v-if="index > 0"
              >
                <v-divider class="brown lighten-5"></v-divider>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="6"
              >
                <v-text-field
                  v-model="work.company"
                  label="Company"
                  counter="150"
                  maxlength="150"
                  hide-details="auto"
                  :rules="[rules.maximum(150)]"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-text-field
                  v-model="work.position"
                  label="Position"
                  counter="50"
                  maxlength="50"
                  hide-details="auto"
                  :rules="[rules.maximum(50)]"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-text-field
                  v-model="work.period"
                  label="Period"
                  counter="50"
                  maxlength="50"
                  hide-details="auto"
                  :rules="[rules.maximum(50)]"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                v-if="index > 0"
              >
                <v-btn
                  color="red darken-1"
                  class="text-capitalize"
                  @click="removeWork(work.id)"
                  small
                  outlined
                >
                  <v-icon left>
                    mdi-delete-outline
                  </v-icon>
                  Remove
                </v-btn>
              </v-col>
            </v-row>
            <v-btn
              color="info"
              class="mt-5"
              small
              @click="addWork"
              outlined
            >
              <v-icon left>
                mdi-plus
              </v-icon>
              Add Experience
            </v-btn>
          </v-card-text>
        </div>
        <v-divider class="mt-2"></v-divider>
        <div class="pa-5">
          <v-card-title>REAL STATE EXPERIENCE&nbsp;<span class="font-italic">(if any)</span></v-card-title>
          <v-card-text>
            <v-row v-for="(state, index) in state_experience" :key="state.id">
              <v-col
                cols="12"
                v-if="index > 0"
              >
                <v-divider class="brown lighten-5"></v-divider>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="6"
              >
                <v-text-field
                  v-model="state.company"
                  label="Company"
                  counter="150"
                  maxlength="150"
                  hide-details="auto"
                  :rules="[rules.maximum(150)]"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-menu
                  v-model="state.inclusive_date_from_menu"
                  :close-on-content-click="false"
                  transition="scale-transition"
                  offset-y
                  min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="state.inclusive_date_from"
                      label="Inclusive Date From"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      @keyup="state.inclusive_date_from_menu=true"
                      @keydown="state.inclusive_date_from_menu=false"
                      hide-details="auto"
                      clearable
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="state.inclusive_date_from"
                    @input="state.inclusive_date_from_menu = false"
                    :max="(new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)"
                  ></v-date-picker>
                </v-menu>
                <!-- <v-text-field
                  v-model="state.inclusive_date"
                  label="Inclusive Date"
                  counter="50"
                  maxlength="50"
                  hide-details="auto"
                  :rules="[rules.maximum(50)]"
                ></v-text-field> -->
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-menu
                  v-model="state.inclusive_date_to_menu"
                  :close-on-content-click="false"
                  transition="scale-transition"
                  offset-y
                  min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="state.inclusive_date_to"
                      label="Inclusive Date To"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      @keyup="state.inclusive_date_to_menu=true"
                      @keydown="state.inclusive_date_to_menu=false"
                      hide-details="auto"
                      clearable
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="state.inclusive_date_to"
                    @input="state.inclusive_date_to_menu = false"
                    :max="(new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)"
                  ></v-date-picker>
                </v-menu>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-text-field
                  v-model="state.average_monthly_production"
                  label="Average Monthly Production"
                  counter="70"
                  maxlength="70"
                  hide-details="auto"
                  :rules="[rules.maximum(70)]"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-text-field
                  v-model="state.average_monthly_allowance"
                  label="Average Monthly Allowance"
                  counter="70"
                  maxlength="70"
                  hide-details="auto"
                  :rules="[rules.maximum(70)]"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                md="6"
              >
                <v-text-field
                  v-model="state.reason_for_leaving"
                  label="Reason for leaving"
                  counter="200"
                  maxlength="200"
                  hide-details="auto"
                  :rules="[rules.maximum(200)]"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                v-if="index > 0"
              >
                <v-btn
                  color="red darken-1"
                  class="text-capitalize"
                  @click="removeState(state.id)"
                  small
                  outlined
                >
                  <v-icon left>
                    mdi-delete-outline
                  </v-icon>
                  Remove
                </v-btn>
              </v-col>
            </v-row>
            <v-btn
              color="info"
              class="mt-5"
              small
              @click="addState"
              outlined
            >
              <v-icon left>
                mdi-plus
              </v-icon>
              Add Experience
            </v-btn>
          </v-card-text>
        </div>
        <v-divider class="mt-2"></v-divider>
        <div class="pa-5">
          <v-card-title>WORK/APPLIED WITH NEW SAN JOSE BUILDERS, INC. BEFORE?</v-card-title>
          <v-card-text>
            <v-radio-group v-model="applied_before" :rules="[rules.required]">
              <v-radio
                v-for="n in ['Yes', 'No']"
                :key="n"
                :label="n"
                :value="n"
              ></v-radio>
            </v-radio-group>
            <v-row>
              <v-col
                cols="12"
                md="6"
              >
                <v-text-field
                  ref="what_focus_project"
                  :rules="[rules.maximum(200)]"
                  v-model="what_focus_project"
                  label="What focus project?"
                  counter="200"
                  maxlength="200"
                  hide-details="auto"
                  v-if="applied_before == 'Yes'"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-menu
                  v-model="wa_nsjbib_exclusive_date_from_menu"
                  :close-on-content-click="false"
                  transition="scale-transition"
                  offset-y
                  min-width="auto"
                  v-if="applied_before == 'Yes'"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="wa_nsjbib_exclusive_date_from"
                      label="Exclusive Date From"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      @keyup="wa_nsjbib_exclusive_date_from_menu=true"
                      @keydown="wa_nsjbib_exclusive_date_from_menu=false"
                      hide-details="auto"
                      clearable
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="wa_nsjbib_exclusive_date_from"
                    @input="wa_nsjbib_exclusive_date_from_menu = false"
                    :max="(new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)"
                  ></v-date-picker>
                </v-menu>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="3"
              >
                <v-menu
                  v-model="wa_nsjbib_exclusive_date_to_menu"
                  :close-on-content-click="false"
                  transition="scale-transition"
                  offset-y
                  min-width="auto"
                  v-if="applied_before == 'Yes'"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="wa_nsjbib_exclusive_date_to"
                      label="Exclusive Date To"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      @keyup="wa_nsjbib_exclusive_date_to_menu=true"
                      @keydown="wa_nsjbib_exclusive_date_to_menu=false"
                      hide-details="auto"
                      clearable
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="wa_nsjbib_exclusive_date_to"
                    @input="wa_nsjbib_exclusive_date_to_menu = false"
                    :max="(new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)"
                  ></v-date-picker>
                </v-menu>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="4"
              >
                <v-text-field
                  ref="wa_nsjbib_monthly_sales"
                  :rules="[rules.maximum(70)]"
                  v-model="wa_nsjbib_monthly_sales"
                  label="Monthly Sales Production"
                  counter="70"
                  maxlength="70"
                  hide-details="auto"
                  v-if="applied_before == 'Yes'"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="4"
              >
                <!-- <p>{{ wa_nsjbib_position }}</p> -->
                <v-autocomplete
                  v-model="wa_nsjbib_position"
                  :items="positions"
                  label="Position"
                  hide-details="auto"
                  return-object
                  v-if="applied_before == 'Yes'"
                ></v-autocomplete>
                <!-- <v-text-field
                  ref="wa_nsjbib_monthly_sales"
                  :rules="[rules.maximum(50)]"
                  v-model="wa_nsjbib_monthly_sales"
                  label="Monthly Sales Production"
                  counter="50"
                  maxlength="50"
                  hide-details="auto"
                  v-if="applied_before == 'Yes'"
                ></v-text-field> -->
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="4"
              >
                <v-text-field
                  ref="wa_nsjbib_sales_group_head"
                  :rules="[rules.maximum(100)]"
                  v-model="wa_nsjbib_sales_group_head"
                  label="Sales Group / Head"
                  counter="100"
                  maxlength="100"
                  hide-details="auto"
                  v-if="applied_before == 'Yes'"
                ></v-text-field>
              </v-col>
            </v-row>
            <p class="font-italic subtitle-1 mt-0 mb-0" :class="applied_before == 'Yes' ? 'mt-4' : ''">Note: Non-disclosure of information shall forfeit your Accreditation.</p>
          </v-card-text>
        </div>
        <v-divider></v-divider>
        <div class="pa-5">
          <v-card-title>PERSON TO NOTIFY IN CASE OF EMERGENCY</v-card-title>
          <v-card-text>
            <v-row>
              <v-col
                cols="12"
                sm="6"
                md="6"
              >
                <v-text-field
                  ref="emergency_name"
                  :rules="[rules.required, rules.alphaSpace, rules.minimum(2), rules.maximum(90)]"
                  v-model="emergency_name"
                  label="Name*"
                  counter="90"
                  maxlength="90"
                  hide-details="auto"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="6"
              >
                <v-text-field
                  ref="emergency_contact"
                  :rules="[rules.required, rules.validMobileNumber]"
                  v-model="emergency_contact"
                  label="Contact Number*"
                  hint="must be a valid mobile number, example: 09293158777"
                  persistent-hint
                  counter="11"
                  maxlength="11"
                  hide-details="auto"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  ref="emergency_address"
                  :rules="[rules.required, rules.maximum(200)]"
                  v-model="emergency_address"
                  label="Address*"
                  counter="200"
                  maxlength="200"
                  hide-details="auto"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-card-text>
        </div>
        <v-divider v-if="[1,2,3].includes(details.position)" class="mt-2"></v-divider>
        <div v-if="[1,2,3].includes(details.position)" class="pa-5">
          <v-card-title>ENDORSEMENT DETAILS</v-card-title>
          <v-card-text>
            <v-row>
              <v-col
                cols="12"
                sm="6"
                md="4"
                v-if="[1,2,3].includes(details.position)"
              >
                <v-autocomplete
                  ref="sales_head"
                  :rules="[rules.required]"
                  v-model="sales_head"
                  :items="sales_heads"
                  label="Sales Head*"
                  hide-details="auto"
                  return-object
                ></v-autocomplete>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="4"
                v-if="[1,2].includes(details.position)"
              >
                <!-- <v-autocomplete
                  ref="project_director"
                  :rules="[rules.required]"
                  v-model="project_director"
                  :items="project_directors"
                  label="Project Director*"
                  hide-details="auto"
                  return-object
                ></v-autocomplete> -->
                <v-text-field
                  ref="project_director"
                  :rules="[rules.required, rules.alphaSpace, rules.minimum(2), rules.maximum(90)]"
                  v-model="project_director"
                  label="Project Director*"
                  counter="90"
                  maxlength="90"
                  hide-details="auto"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                md="4"
                v-if="[1].includes(details.position)"
              >
                <!-- <v-autocomplete
                  ref="sales_director"
                  :rules="[rules.required]"
                  v-model="sales_director"
                  :items="sales_directors"
                  label="Sales Director*"
                  hide-details="auto"
                  return-object
                ></v-autocomplete> -->
                <v-text-field
                  ref="sales_director"
                  :rules="[rules.required, rules.alphaSpace, rules.minimum(2), rules.maximum(90)]"
                  v-model="sales_director"
                  label="Sales Director*"
                  counter="90"
                  maxlength="90"
                  hide-details="auto"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-card-text>
        </div>
        <v-divider class="mt-2"></v-divider>
        <div class="pa-5">
          <v-row>
            <v-col cols="12" md="6">
              <v-card-title>ATTACH RESUME</v-card-title>
              <v-card-text>
                <v-file-input
                  ref="file"
                  :rules="[rules.required, rules.fileSize(3)]"
                  v-model="file"
                  accept=".pdf,.doc,.docx"
                  prepend-icon="mdi-file-document-outline"
                  show-size
                  hint="Your file must be in PDF (.pdf) or DOC (.doc,.docx) format, File size must not exceed 3MB"
                  persistent-hint
                  label="Select File*"
                ></v-file-input>
              </v-card-text>
            </v-col>
            <v-col cols="12" md="6">
              <v-card-title>ATTACH NBI CLEARANCE</v-card-title>
              <v-card-text>
                <v-file-input
                  :rules="[rules.fileSize(5)]"
                  v-model="file_nbi"
                  accept=".png,.jpg,.jpeg"
                  prepend-icon="mdi-file-document-outline"
                  show-size
                  hint="Your file must be in PNG (.png) or JPG (.jpg,.jpeg) format, File size must not exceed 5MB"
                  persistent-hint
                  label="Select File"
                ></v-file-input>
              </v-card-text>
            </v-col>
          </v-row>
          <v-card-text>
            <v-btn
              :disabled="!valid"
              type="submit"
              color="blue darken-3 white--text"
              :loading="isFormLoading"
            >
              <v-icon left>
                mdi-send-outline
              </v-icon>
              Submit
            </v-btn>
          </v-card-text>
        </div>
      </v-form>
    </v-card>
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
    >
      <v-card>
        <v-toolbar
          flat
          dark
          elevation="2"
          color="blue darken-3"
          class="rounded-0"
        >
          <v-btn
            icon
            dark
            @click="dialog = false"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Preview</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn
              dark
              text
              @click="handleContinue"
            >
              Continue
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-card-text class="pl-0 pr-0">
          <v-row>
            <v-col cols="12" md="6">
              <p class="text-h6 mx-3 my-4">PERSONAL INFORMATION</p>
              <v-simple-table dense>
                <template v-slot:default>
                  <tbody>
                    <tr>
                      <td class="text-caption" :style="{ width: tdWidth }">Surname</td>
                      <td class="text-subtitle-2">{{ surname }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Given Name</td>
                      <td class="text-subtitle-2">{{ given_name }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Middle Name</td>
                      <td class="text-subtitle-2">{{ middle_name }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Nickname</td>
                      <td class="text-subtitle-2">{{ nickname }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Address</td>
                      <td class="text-subtitle-2">{{ address }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Birthdate</td>
                      <td class="text-subtitle-2">{{ formatDate(birthdate) }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Age</td>
                      <td class="text-subtitle-2">{{ getAge }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Gender</td>
                      <td class="text-subtitle-2">{{ gender }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Civil Status</td>
                      <td class="text-subtitle-2">{{ civil_status }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Contact Number</td>
                      <td class="text-subtitle-2">{{ contact }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Email Address</td>
                      <td class="text-subtitle-2">{{ details.email }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">TIN</td>
                      <td class="text-subtitle-2">{{ tin }}</td>
                    </tr>
                    <!-- <tr>
                      <td class="text-caption">SSS</td>
                      <td class="text-subtitle-2">{{ sss }}</td>
                    </tr> -->
                  </tbody>
                </template>
              </v-simple-table>
              <p class="text-h6 mx-3 my-4">EDUCATIONAL ATTAINMENT</p>
              <v-simple-table v-if="educationPreview.length > 0" dense>
                <template v-slot:default>
                  <tbody
                    v-for="(item, index) in educationPreview"
                    :key="index"
                  >
                    <tr>
                      <td colspan="2">
                        <v-chip>{{ index + 1 }}</v-chip>
                      </td>
                    </tr>
                    <tr>
                      <td class="text-caption" :style="{ width: tdWidth }">Level</td>
                      <td class="text-subtitle-2">{{ item.level }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">School</td>
                      <td class="text-subtitle-2">{{ item.school }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Course</td>
                      <td class="text-subtitle-2">{{ item.course }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
              <v-simple-table v-else dense>
                <template v-slot:default>
                  <tbody>
                    <tr>
                      <td colspan="2">-</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
              <v-row>
                <v-col cols="12" md="6">
                  <p class="text-h6 mx-3 my-4">RESUME</p>
                  <v-simple-table dense>
                    <template v-slot:default>
                      <tbody>
                        <tr>
                          <td class="text-caption" v-if="file">{{ file.name }}</td>
                          <td class="text-caption" v-else>No File</td>
                        </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                </v-col>
                <v-col cols="12" md="6">
                  <p class="text-h6 mx-3 my-4">NBI CLEARANCE</p>
                  <v-simple-table dense>
                    <template v-slot:default>
                      <tbody>
                        <tr>
                          <td class="text-caption" v-if="file_nbi">{{ file_nbi.name }}</td>
                          <td class="text-caption" v-else>No File</td>
                        </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                </v-col>
              </v-row>
            </v-col>
            <v-col cols="12" md="6">
              <p class="text-h6 mx-3 my-4">RECENT WORK EXPERIENCE</p>
              <v-simple-table v-if="workPreview.length > 0" dense>
                <template v-slot:default>
                  <tbody
                    v-for="(item, index) in workPreview"
                    :key="index"
                  >
                    <tr>
                      <td colspan="2">
                        <v-chip>{{ index + 1 }}</v-chip>
                      </td>
                    </tr>
                    <tr>
                      <td class="text-caption" :style="{ width: tdWidth }">Company</td>
                      <td class="text-subtitle-2">{{ item.company }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Position</td>
                      <td class="text-subtitle-2">{{ item.position }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Period</td>
                      <td class="text-subtitle-2">{{ item.period }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
              <v-simple-table v-else dense>
                <template v-slot:default>
                  <tbody>
                    <tr>
                      <td colspan="2">-</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
              <p class="text-h6 mx-3 my-4">REAL STATE EXPERIENCE</p>
              <v-simple-table v-if="statePreview.length > 0" dense>
                <template v-slot:default>
                  <tbody
                    v-for="(item, index) in statePreview"
                    :key="index"
                  >
                    <tr>
                      <td colspan="2">
                        <v-chip>{{ index + 1 }}</v-chip>
                      </td>
                    </tr>
                    <tr>
                      <td class="text-caption" :style="{ width: tdWidth }">Company</td>
                      <td class="text-subtitle-2">{{ item.company }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Inclusive Date From</td>
                      <td class="text-subtitle-2">{{ (item.inclusive_date_from) ? formatDate(item.inclusive_date_from) : '' }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Inclusive Date To</td>
                      <td class="text-subtitle-2">{{ (item.inclusive_date_to) ? formatDate(item.inclusive_date_to) : '' }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Average Monthly Production</td>
                      <td class="text-subtitle-2">{{ item.average_monthly_production }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Average Monthly Allowance</td>
                      <td class="text-subtitle-2">{{ item.average_monthly_allowance }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Reason for leaving</td>
                      <td class="text-subtitle-2">{{ item.reason_for_leaving }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
              <v-simple-table v-else dense>
                <template v-slot:default>
                  <tbody>
                    <tr>
                      <td colspan="2">-</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
              <p class="text-h6 mx-3 my-4">WORK/APPLIED WITH NEW SAN JOSE BUILDERS, INC. BEFORE?</p>
              <v-simple-table dense>
                <template v-slot:default>
                  <tbody>
                    <tr>
                      <td class="text-caption font-weight-bold" colspan="2">{{ applied_before }}</td>
                    </tr>
                    <tr v-if="applied_before == 'Yes'">
                      <td class="text-caption" :style="{ width: tdWidth }">What focus project?</td>
                      <td class="text-subtitle-2">{{ what_focus_project }}</td>
                    </tr>
                    <tr v-if="applied_before == 'Yes'">
                      <td class="text-caption">Exclusive Date From</td>
                      <td class="text-subtitle-2">{{ (wa_nsjbib_exclusive_date_from) ? formatDate(wa_nsjbib_exclusive_date_from) : '' }}</td>
                    </tr>
                    <tr v-if="applied_before == 'Yes'">
                      <td class="text-caption">Exclusive Date To</td>
                      <td class="text-subtitle-2">{{ (wa_nsjbib_exclusive_date_to) ? formatDate(wa_nsjbib_exclusive_date_to) : '' }}</td>
                    </tr>
                    <tr v-if="applied_before == 'Yes'">
                      <td class="text-caption">Monthly Sales Production</td>
                      <td class="text-subtitle-2">{{ wa_nsjbib_monthly_sales }}</td>
                    </tr>
                    <tr v-if="applied_before == 'Yes'">
                      <td class="text-caption">Position</td>
                      <td class="text-subtitle-2">{{ wa_nsjbib_position.text }}</td>
                    </tr>
                    <tr v-if="applied_before == 'Yes'">
                      <td class="text-caption">Sales Group / Head</td>
                      <td class="text-subtitle-2">{{ wa_nsjbib_sales_group_head }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
              <p class="text-h6 mx-3 my-4">PERSON TO NOTIFY IN CASE OF EMERGENCY</p>
              <v-simple-table dense>
                <template v-slot:default>
                  <tbody>
                    <tr>
                      <td class="text-caption" :style="{ width: tdWidth }">Name</td>
                      <td class="text-subtitle-2">{{ emergency_name }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Contact Number</td>
                      <td class="text-subtitle-2">{{ emergency_contact }}</td>
                    </tr>
                    <tr>
                      <td class="text-caption">Address</td>
                      <td class="text-subtitle-2">{{ emergency_address }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
              <p v-if="[1,2,3].includes(details.position)" class="text-h6 mx-3 my-4">ENDORSEMENT DETAILS</p>
              <v-simple-table v-if="[1,2,3].includes(details.position)" dense>
                <template v-slot:default>
                  <tbody>
                    <tr v-if="[1,2,3].includes(details.position)">
                      <td class="text-caption" :style="{ width: tdWidth }">Sales Head</td>
                      <td class="text-subtitle-2">{{ sales_head.text }}</td>
                    </tr>
                    <tr v-if="[1,2].includes(details.position)">
                      <td class="text-caption">Project Director</td>
                      <td class="text-subtitle-2">{{ project_director }}</td>
                    </tr>
                    <tr v-if="[1].includes(details.position)">
                      <td class="text-caption">Sales Director</td>
                      <td class="text-subtitle-2">{{ sales_director }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="dialogFinal"
      persistent
      width="800"
    >
      <v-card>
        <v-card-title>
          <span>Data Privacy Consent</span>
          <v-spacer></v-spacer>
          <a
            href="#"
            @click.prevent="showDataPrivacy"
            class="grey--text data-privacy-link"
          >
            Data Privacy
            <v-icon x-small>mdi-tray-arrow-up</v-icon>
          </a>
        </v-card-title>
        <v-card-text>
          <p class="text-subtitle-2">
            I hereby allow New San Jose Builders, Inc. (“NSJBI”) to collect, hold, process, use, store, or disclose my personal information, whenever necessary, among its officers and employees, government authorities, and to necessary third parties relative to my account/s with the company for the purpose of processing my transaction/s which I and/or my authorized representative have requested.
          </p> 
          <div class="mt-5">
            <v-btn
              color="blue darken-3"
              class="px-5 mr-3 white--text"
              @click="handleSubmit"
            >
              Agree and Continue
            </v-btn>
            <v-btn
              class="px-5"
              @click="dialogFinal = false"
            >
              Cancel
            </v-btn>
          </div>
        </v-card-text>  
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="dialogDataPrivacy"
      width="800"
    >
      <v-card>
        <v-card-title class="grey lighten-2">
          <span class="text-h5">Data Privacy Act Statement</span>
          <v-spacer></v-spacer>
          <v-btn
            icon
            @click="dialogDataPrivacy = false"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>

        <v-card-text>
          <p class="pt-5">Dear Valued Client,</p>
          <p>In compliance with Republic Act (“R.A.”) No. 10173, otherwise known as the “Data Privacy Act of 2012”, New San Jose Builders, Inc. (“NSJBI”) would like to secure your consent on the use of your personal information and/or sensitive personal information (both herein called as “personal information”) which we will and have gathered during the course of your transaction/s with NSJBI.</p>
          <p>Upon your consent, your personal information may be collected, held, processed, stored, used, or disclosed among employees, members, or officers of NSJBI, government authorities, and to third parties,  whenever necessary, for the following purposes: (1) securing or updating your account/s; (2) processing of in-house financing, if applicable; (3) processing of external financing, if applicable; and (4) other legitimate and analogous transaction/s concerning your account/s which you and/or your authorized representative have requested from us.</p>
          <p>Your consent will allow any employees, members, or officers of NSJBI to process your current and future transactions more efficiently, with less inconvenience on your part. As your developer, be assured that NSJBI is committed in ensuring that all transaction/s relative to your account/s with us is/are processed efficiently while securing the confidentiality of your personal information against unauthorized use or disclosure.</p>
          <p>If you also wish to access, update, correct, withdraw your consent, or should you have any questions regarding the handling of your personal information, you may visit our office anytime during office hours. You may also visit the website of the National Privacy Commission at https://www.privacy.gov.ph where you may also find the Data Privacy Act of 2012 as your reference.</p>
          <p>Thank you very much.</p>
          <p class="text-right">Sincerely yours,</p>
          <p class="text-right font-weight-bold">New San Jose Builders, Inc. Management</p>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { mapState, mapActions } from "vuex"
import { isObjectEmpty, errorFocus } from '@/helpers/form'
import formRules from '@/helpers/formRules'
// import uplines from '@/data/uplines.json'
import positions from '@/data/positions.json'
import { format } from 'date-fns'

export default {
  props: {
    details: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    valid: true,
    surname: '',
    given_name: '',
    middle_name: '',
    nickname: '',
    address: '',
    birthdate: '',
    birthdate_menu: false,
    age: '',
    gender: '',
    gender_choices: [
      'Male',
      'Female'
    ],
    civil_status: '',
    civil_status_choices: [
      'Single',
      'Married',
      'Divorced',
      'Widowed'
    ],
    contact: '',
    tin: '',
    // sss: '',
    educCount: 0,
    educational_attainment: [
      {
        id: 'educAtt0',
        level: '',
        school: '',
        course: ''
      }
    ],
    workCount: 0,
    work_experience: [
      {
        id: 'workExp0',
        company: '',
        position: '',
        period: ''
      }
    ],
    stateCount: 0,
    state_experience: [
      {
        id: 'stateExp0',
        company: '',
        inclusive_date_from: '',
        inclusive_date_from_menu: false,
        inclusive_date_to: '',
        inclusive_date_to_menu: false,
        average_monthly_production: '',
        average_monthly_allowance: '',
        reason_for_leaving: ''
      }
    ],
    applied_before: 'No',
    what_focus_project: '',
    wa_nsjbib_exclusive_date_from: '',
    wa_nsjbib_exclusive_date_from_menu: false,
    wa_nsjbib_exclusive_date_to: '',
    wa_nsjbib_exclusive_date_to_menu: false,
    wa_nsjbib_monthly_sales: '',
    wa_nsjbib_position: '',
    positions: positions,
    wa_nsjbib_sales_group_head: '',
    emergency_name: '',
    emergency_contact: '',
    emergency_address: '',
    uplines: [],
    sales_head: '',
    project_director: '',
    sales_director: '',
    file: null,
    file_nbi: null,
    isChecked: false,
    dialog: false,
    dialogFinal: false,
    rules: formRules,
    tdWidth: '25%',
    dialogDataPrivacy: false
  }),
  computed: {
    ...mapState("applicant", ["isFormLoading", "formError", "formErrors"]),
    getAge() {
      if (this.birthdate) {
        var today = new Date()
        var birthDate = new Date(this.birthdate)
        var age = today.getFullYear() - birthDate.getFullYear()
        var m = today.getMonth() - birthDate.getMonth()
        if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
          age--
        }
        return age
      }
      return 0
    },
    educationPreview() {
      return this.educational_attainment.filter((item) => {
        return item.level || item.school || item.course
      })
    },
    workPreview() {
      return this.work_experience.filter((item) => {
        return item.company || item.position || item.period
      })
    },
    statePreview() {
      return this.state_experience.filter((item) => {
        return item.company || item.inclusive_date_from || item.inclusive_date_to || item.average_monthly_production || item.average_monthly_allowance || item.reason_for_leaving
      })
    },
    sales_heads() {
      return this.uplines.filter((upline) => {
        return upline.position_id == 4 // && upline.sales_group_id == this.details.sales_group
      })
    },
    project_directors() {
      return this.uplines.filter((upline) => {
        return upline.position_id == 3 // && upline.sales_group_id == this.details.sales_group
      })
    },
    sales_directors() {
      return this.uplines.filter((upline) => {
        return upline.position_id == 2 // && upline.sales_group_id == this.details.sales_group
      })
    }
  },
  mounted() {
    this.$refs.surname.focus()
    this.surname = this.details.surname || ''
    this.given_name = this.details.given_name || ''
    this.middle_name = this.details.middle_name || ''
    this.nickname = this.details.nickname || ''
    this.contact = this.details.contact || ''
    this.uplines = this.details.uplines || []
  },
  methods: {
    ...mapActions("applicant", ["formSubmit"]),
    addEduc() {
      this.educational_attainment.push(
        {
          id: `educAtt${++this.educCount}`,
          level: '',
          school: '',
          course: ''
        }
      )
    },
    removeEduc(id) {
      this.educational_attainment = this.educational_attainment.filter(item => {
        return item.id != id
      })
    },
    addWork() {
      this.work_experience.push(
        {
          id: `workExp${++this.workCount}`,
          company: '',
          position: '',
          period: ''
        }
      )
    },
    removeWork(id) {
      this.work_experience = this.work_experience.filter(item => {
        return item.id != id
      })
    },
    addState() {
      this.state_experience.push(
        {
          id: `stateExp${++this.stateCount}`,
          company: '',
          inclusive_date_from: '',
          inclusive_date_from_menu: false,
          inclusive_date_to: '',
          inclusive_date_to_menu: false,
          average_monthly_production: '',
          average_monthly_allowance: '',
          reason_for_leaving: ''
        }
      )
    },
    removeState(id) {
      this.state_experience = this.state_experience.filter(item => {
        return item.id != id
      })
    },
    handleConfirm() {
      this.dialog = true
    },
    formatDate(date) {
      return format(new Date(date), 'MMM dd, yyyy')
    },
    handleContinue() {
      if (!this.isFormLoading) {
        this.dialogFinal = true
      }
    },
    async handleSubmit () {
      this.dialogFinal = false
      this.dialog = false
      
      const updateData = {
        code: this.details.code,
        surname: this.surname,
        given_name: this.given_name,
        middle_name: this.middle_name,
        nickname: this.nickname,
        address: this.address,
        birthdate: this.birthdate,
        gender: this.gender,
        civil_status: this.civil_status,
        contact: this.contact,
        tin: this.tin,
        education: this.educational_attainment,
        work_exp: this.work_experience,
        state_exp: this.state_experience,
        applied_before: this.applied_before,
        what_focus_project: this.what_focus_project,
        wa_nsjbib_exclusive_date_from: this.wa_nsjbib_exclusive_date_from,
        wa_nsjbib_exclusive_date_to: this.wa_nsjbib_exclusive_date_to,
        wa_nsjbib_monthly_sales: this.wa_nsjbib_monthly_sales,
        wa_nsjbib_position: this.wa_nsjbib_position.value,
        wa_nsjbib_sales_group_head: this.wa_nsjbib_sales_group_head,
        emergency_name: this.emergency_name,
        emergency_contact: this.emergency_contact,
        emergency_address: this.emergency_address,
        sales_head: this.sales_head.value,
        project_director: this.project_director,
        sales_director: this.sales_director,
        file: this.file,
        file_nbi: this.file_nbi
      }

      // console.log(updateData)
      
      if (!this.isFormLoading) {
        await this.formSubmit(updateData)

        if (!this.formError && isObjectEmpty(this.formErrors)) {
          this.$emit('formCompleted')
        } else {
          if (!isObjectEmpty(this.formErrors)) {
            // display errors
            // Object.entries(this.formErrors).forEach(([key, value]) => {
            //   this[`${key}Rules`] = []

            //   if (value) {
            //     this[`${key}Rules`].push(value)
            //   }
            // })

            this.$refs.form.validate()

            // focus in first error
            const focusError = errorFocus(this.formErrors)
            this.$refs[focusError].focus()
          } else {
            this.$refs.form.resetValidation()
          }
        }
      }
    },
    showDataPrivacy() {
      this.dialogDataPrivacy = true
    }
  }
}
</script>

<style scoped>
.data-privacy-link {
  font-size: 11px; 
  text-decoration: none;
  transition: 500ms;
}
.data-privacy-link:hover {
  color: #1565C0 !important
}
</style>