<template>
  <div>
    <div v-if="isLoading">
      <v-container fluid pa-0>
        <v-row 
          align="center" 
          justify="center" 
          style="height: 100vh" 
          dense
        >
          <v-col 
            cols="12" 
            lg="6" 
            md="6" 
            class="fill-height d-flex flex-column justify-center align-center"
          >
            <v-card 
              flat 
              tile
            >
              <v-card-text>
                <v-progress-circular
                  :size="70"
                  :width="7"
                  color="blue darken-3"
                  indeterminate
                ></v-progress-circular>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </div>
    <div v-else>
      <div v-if="error">
        {{ error }}
      </div>
      <div v-else>
        <Form v-if="details.fillup_form" :details="{ ...details, code: code }" @formCompleted="formCompleted" />
        <Test v-if="!details.fillup_form && activeTest" :details="{ ...activeTest, code: code, hasNextTest: hasNextTest }" @updateTest="updateTest" @testStarted="testStarted" @testAnswered="testAnswered" @openRetake="openRetake" @updateResult="updateResult" @updateComputationFile="updateComputationFile"/>
        <Completed v-if="!details.fillup_form && !activeTest" :details="{ ...details, code: code }" />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex"
import Form from '@/components/applicant/Form'
import Test from '@/components/applicant/Test'
import Completed from '../components/applicant/Completed.vue'

export default {
  components: { Form, Test, Completed },
  props: {
    code: {
      type: String,
      required: true
    }
  },
  data: () => ({
    activeTest: {}
  }),
  created() {
    this.doGetDetails()
  },
  computed: {
    ...mapState("applicant", ["details", "isLoading", "error"]),
    hasNextTest() {
      return this.details.tests.filter((test) => {
        return test.is_active == true && test.id != this.activeTest.id
      }).length
    }
  },
  methods: {
    ...mapActions("applicant", ["getDetails"]),
    async doGetDetails() {
      if (!this.isLoading) {
        this.$store.commit("applicant/reset")
        await this.getDetails(this.code)

        this.firstActiveTest()
      }
    },

    firstActiveTest() {
      this.activeTest = this.details.tests.find((test) => test.is_active == true)
    },

    formCompleted() {
      this.details.fillup_form = false
      this.firstActiveTest()
    },

    testStarted(id, started_at, expired_at) {
      this.details.tests.find((test) => test.id == id).started_at = started_at
      this.details.tests.find((test) => test.id == id).expired_at = expired_at
    },

    testAnswered(id, question_id, type, answer) {
      if (type==1) {
        this.details.tests.find((test) => test.id == id).questions.find((question) => question.id == question_id).answer_id = answer
      }
      if (type==2) {
        this.details.tests.find((test) => test.id == id).questions.find((question) => question.id == question_id).raw_answer = answer
      }
      this.details.tests.find((test) => test.id == id).questions.find((question) => question.id == question_id).has_answered = true
    },

    openRetake() {
      this.details.tests.find((test) => test.id == 2).is_active = true
    },

    updateResult(id, result, correct_answer, wrong_answer) {
      this.details.tests.find((test) => test.id == id).result = result
      this.details.tests.find((test) => test.id == id).correct_answer = correct_answer
      this.details.tests.find((test) => test.id == id).wrong_answer = wrong_answer
    },

    updateComputationFile(id, file_name) {
      this.details.tests.find((test) => test.id == id).computation_file = file_name
    },
    
    updateTest(id) {
      this.details.tests.find((test) => test.id == id).is_active = false

      this.firstActiveTest()
    }
  }
}
</script>