<template>
  <div>
    <!-- <div class="day">
      <span class="number">{{ days }}</span>
      <div class="format">{{ wordString.day }}</div>
    </div> -->
    <div class="hour" v-if="hours > 0">
      <span class="number">{{ addLeadingZero(hours) }}{{ hours }}</span>
      <div class="format">{{ wordString.hours }}</div>
    </div>
    <div class="min">
      <span class="number">{{ addLeadingZero(minutes) }}{{ minutes }}</span>
      <div class="format">{{ wordString.minutes }}</div>
    </div>
    <div class="sec">
      <span class="number">{{ addLeadingZero(seconds) }}{{ seconds }}</span>
      <div class="format">{{ wordString.seconds }}</div>
    </div>
    <!-- <div class="message">{{ message }}</div>
    <div class="status-tag" :class="statusType">{{ statusText }}</div> -->
  </div>
</template>

<script>
export default {
  props: ['starttime', 'endtime', 'trans'],
  data: () => ({
    timer:"",
    wordString: {},
    start: "",
    end: "",
    interval: "",
    days:"",
    minutes:"",
    hours:"",
    seconds:"",
    message:"",
    statusType:"",
    statusText: ""
  }),
  created: function () {
    this.wordString = JSON.parse(this.trans);
  },
  mounted(){
    this.start = new Date(this.starttime).getTime();
    this.end = new Date(this.endtime).getTime();
    // Update the count down every 1 second
    this.timerCount(this.start,this.end);
    this.interval = setInterval(() => {
      this.timerCount(this.start,this.end);
    }, 1000);
  },
  methods: {
    timerCount: function(start,end){
      // Get todays date and time
      var now = new Date().getTime();

      // Find the distance between now an the count down date
      var distance = start - now;
      var passTime =  end - now;

      if(distance < 0 && passTime < 0){
        this.message = this.wordString.expired;
        this.statusType = "expired";
        this.statusText = this.wordString.status.expired;
        this.timeEnd()
        clearInterval(this.interval);
      }else if(distance < 0 && passTime > 0){
        this.calcTime(passTime);
        this.message = this.wordString.running;
        this.statusType = "running";
        this.statusText = this.wordString.status.running;
      } else if( distance > 0 && passTime > 0 ){
        this.calcTime(distance); 
        this.message = this.wordString.upcoming;
        this.statusType = "upcoming";
        this.statusText = this.wordString.status.upcoming;
      }
    },
    calcTime: function(dist){
      // Time calculations for days, hours, minutes and seconds
      this.days = Math.floor(dist / (1000 * 60 * 60 * 24));
      this.hours = Math.floor((dist % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      this.minutes = Math.floor((dist % (1000 * 60 * 60)) / (1000 * 60));
      this.seconds = Math.floor((dist % (1000 * 60)) / 1000);

      const numEle = document.querySelectorAll('.number')

      if (this.hours==0 && this.minutes==0 && this.seconds <= 60) {
        numEle.forEach((ele) => {
          ele.style.backgroundColor = '#C62828'
          ele.style.borderColor = '#B71C1C'
        })
      } else {
        numEle.forEach((ele) => {
          ele.style.backgroundColor = '#1565C0'
          ele.style.borderColor = '#0D47A1'
        })
      }
    },
    addLeadingZero(val) {
      return (val <= 9) ? '0' : ''
    },
    timeEnd() {
      this.$emit('expired')
    }
  }
}
</script>

<style>
.timer {
  font-size: 20px;
  color: #fff;
  text-align:center;
  margin-top: 50px;
}
.day, .hour, .min, .sec {
  font-size: 30px;
  display: inline-block;
  font-weight: 500;
  text-align: center;
  margin: 0 5px; 
}
.format {
  font-weight: 300;
  font-size: 14px;
  opacity: 0.8;
  width: 60px;
}
.number{
  background: #1565C0;
  padding: 0 5px;
  border-radius: 3px;
  display: inline-block;
  width: 65px;
  text-align: center;
  color: #fff;
  border: 1px solid;
}
.message {
  font-size: 14px;
  font-weight: 400;
  margin-top: 5px;
}
.status-tag{
  width: 270px;
  margin: 10px auto;
  padding: 8px 0;
  font-weight: 500;
  color: #000;
  text-align: center;
  border-radius: 15px;
}
</style>